nav {
    width:100%;
    padding:0 6rem;
    margin: 0 auto;
    height: 5rem;
    background-color: white;
}


.navbar-brand {
    font-size: 2.2rem;
    font-family:Montserrat-VariableFont_wght;
}

.navbar-brand img {
    width: 7.2rem;
    height:auto;
}

nav ul{
    margin-left:2rem;
}


.links li{
    margin:0 1.2rem;
}

.premium li{
    margin:0 0.6rem;
}

nav ul li a{
    font-size:1rem;
    color: rgb(35, 35, 35)!important;
}

nav button {
    border-radius: 20px!important;
    width:8rem;
}