.Premium {
    height:65vh;
    padding-top:5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.premiumIndex {
    

    display: flex;
    flex-direction: column;
    justify-content: center;
    gap:2.5rem;
    color:white;   
    margin: 0 auto;
}

.premiumText {
    display: flex;
    flex-direction: column;
    gap:2rem;
}

.premiumBtns {
    display: flex;
    flex-direction: row;
    gap:1.2rem;
}

.premiumIndex h1{
    font-family: Montserrat-VariableFont_wght;
    font-weight: 600;
    font-size: 2.5rem;
    line-height: 2.9rem;
}

.premiumIndex h2{
    font-family: Montserrat-VariableFont_wght_2;
    font-size: 1.2rem;
    line-height: 1.5rem;
}

.premiumIndex button {
    border-radius: 15px;
}

.premiumContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 4rem;
    
}

.premiumHeading {
    display: flex;
    align-items: center;
    margin-top: 2rem;
}

.premiumHeading h1 {
    font-family: Montserrat-VariableFont_wght;
    font-weight: 600;
    font-size: 2.5rem;
    color: black;
    line-height: 3.5rem;
}